
:root { --bg: #0f172a; --card:#111827; --muted:#9ca3af; --text:#e5e7eb; --primary:#3b82f6; --accent:#22c55e; --border:#1f2937; --radius:12px; --shadow:0 8px 24px rgba(0,0,0,0.35); }
*{box-sizing:border-box} body{margin:0;font-family:Inter,system-ui;background:var(--bg);color:var(--text);} .container{max-width:1100px;margin:28px auto;padding:0 16px;}
.navbar{display:flex;justify-content:space-between;align-items:center;padding:14px;border-bottom:1px solid var(--border);background:rgba(8,12,22,0.6);} .brand{font-weight:700;display:flex;gap:8px;align-items:center;}
.btn{padding:8px 12px;border-radius:10px;border:1px solid var(--border);background:transparent;color:var(--text);cursor:pointer;} .btn-primary{border-color:rgba(59,130,246,0.5);}
.card{background:linear-gradient(180deg,rgba(17,24,39,0.9),rgba(11,18,32,0.9));padding:18px;border-radius:12px;border:1px solid var(--border);box-shadow:var(--shadow);}
.table-wrap{overflow:auto;border-radius:10px;border:1px solid var(--border);padding:8px;}
.select,.input,.number,.date{padding:10px;border-radius:10px;border:1px solid var(--border);background:rgba(8,12,22,0.8);color:var(--text);}
.badge{padding:6px 8px;border-radius:999px;font-size:12px;border:1px solid var(--border);} .badge.in{border-color:rgba(34,197,94,0.6);} .badge.out{border-color:rgba(239,68,68,0.6);}
